import * as React from "react";
import Marquee from "react-fast-marquee";
import laneImg from "../../images/Lane.png";
import seltzerImg from "../../images/Seltzer.png";
import sippyImg from "../../images/Sippy.png";
import snowLeopardImg from "../../images/SnowLeopard.png";
import vivaCiderImg from "../../images/VIVA_Cider.png";

export default function HeroMarquee() {
  const widt = 300;
  const heig = 400;
  const items = [
    {
      id: 1,
      fileSrc: laneImg,
      label: "Lane",
      width: widt,
      height: heig,
    },
    {
      id: 2,
      fileSrc: seltzerImg,
      label: "Seltzer",
      width: widt,
      height: heig,
    },
    {
      id: 3,
      fileSrc: sippyImg,
      label: "Sippy",
      width: widt,
      height: heig,
    },
    {
      id: 4,
      fileSrc: snowLeopardImg,
      label: "SnowLeopard",
      width: widt,
      height: heig,
    },
    {
      id: 5,
      fileSrc: vivaCiderImg,
      label: "VIVA Cider",
      width: widt + widt,
      height: heig,
    },
  ];

  return (
    <div className={`row`}>
      <div className="col">
        <Marquee>
          {items.map((item, idx) => {
            return (
              <span
                data-bs-toggle="tooltip"
                data-bs-placement="top"
                data-bs-title="Tooltip on top"
                key={idx}
              >
                <img
                  alt={item.label}
                  src={item.fileSrc}
                  width={item.widt}
                  height={item.heig}
                />
              </span>
            );
          })}
        </Marquee>
      </div>
    </div>
  );
}
